"use client"
import Sidebar from "@/app/components/ui/Sidebar";
import { useState } from "react"
import Image from "next/image";
import {motion} from 'framer-motion'
import PromptBox from "@/app/components/ui/prompt";
import Message from "@/app/components/ui/message";



export default function Dashboard() {
    
    const [message, setMessage] = useState([]);
    const [isLoading, setIsLoading] = useState(false);


    return <div className="flex  w-full h-screen bg-gradient-to-r from-indigo-100 via-purple-100 to-pink-100">
        <div className="left-0 sticky mr-96">  <Sidebar/>  </div>
        <div className="flex flex-col  w-full  justify-center  "> 
        {message.length == 0 ? (<div className=" justify-center mx-80 " >
            <motion.div   initial={{ opacity: 0, scale: 0 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{
                duration: 0.4,
                scale: { type: "spring", visualDuration: 0.4, bounce: 0.5,  },
            }} className=" flex  rounded-full  "><Image alt="pic" className=" flex justify-center rounded-full outline-1  mr-72 outline-purple-300 border-2 border-purple-200 bg-purple-100 p-1" width={60} height={60} src={'/logo.png'}/> </motion.div>
            <motion.p  animate={{ scale: 1.2 }} className="flex justify-start  mr-40 text-3xl text-purple-800 font-bold mt-1.5"> Asong  </motion.p> 
            <p> </p>
             </div>): (<div className="flex  w-full justify-center  mr-72 "> 
                <Message role="user" content="Hi it is me"/>
                </div>)}
                <div  className={`${message.length == 0 ? "mt-2": "mt-72"}`}> <PromptBox setIsLoading={setIsLoading} isLoading={isLoading}/></div>
                </div>
               
       
    </div>

}